:root {
  --border-radius: 5px;
  --box-shadow: 2px 2px 10px;
  --color-accent: rgba(17, 139, 238, .082);
  --color-bg: #fff;
  --color-bg-secondary: #e9e9e9;
  --color-link: #118bee;
  --color-secondary: #920de9;
  --color-secondary-accent: rgba(146, 13, 233, .043);
  --color-shadow: #f4f4f4;
  --color-table: #118bee;
  --color-text: #000;
  --color-text-secondary: #999;
  --font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  --hover-brightness: 1.2;
  --justify-important: center;
  --justify-normal: left;
  --line-height: 1.5;
  --width-card: 285px;
  --width-card-medium: 460px;
  --width-card-wide: 800px;
  --width-content: 1080px;
}

@media (prefers-color-scheme: dark) {
  :root[color-mode="user"] {
    --color-accent: rgba(0, 151, 252, .31);
    --color-bg: #333;
    --color-bg-secondary: #555;
    --color-link: #0097fc;
    --color-secondary: #e20de9;
    --color-secondary-accent: rgba(226, 13, 233, .31);
    --color-shadow: rgba(187, 187, 187, .125);
    --color-table: #0097fc;
    --color-text: #f7f7f7;
    --color-text-secondary: #aaa;
  }
}

html {
  scroll-behavior: smooth;
}

@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
}

article aside {
  background: var(--color-secondary-accent);
  border-left: 4px solid var(--color-secondary);
  padding: .01rem .8rem;
}

body {
  background: var(--color-bg);
  color: var(--color-text);
  font-family: var(--font-family);
  line-height: var(--line-height);
  margin: 0;
  padding: 0;
  overflow-x: auto;
}

footer, header, main {
  max-width: var(--width-content);
  margin: 0 auto;
  padding: 3rem 1rem;
}

hr {
  background-color: var(--color-bg-secondary);
  border: none;
  width: 100%;
  height: 1px;
  margin: 4rem 0;
}

section {
  justify-content: var(--justify-important);
  flex-wrap: wrap;
  display: flex;
}

section img, article img {
  max-width: 100%;
}

section pre {
  overflow: auto;
}

section aside {
  border: 1px solid var(--color-bg-secondary);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow) var(--color-shadow);
  width: var(--width-card);
  margin: 1rem;
  padding: 1.25rem;
}

section aside:hover {
  box-shadow: var(--box-shadow) var(--color-bg-secondary);
}

[hidden] {
  display: none;
}

article header, div header, main header {
  padding-top: 0;
}

header {
  text-align: var(--justify-important);
}

header a b, header a em, header a i, header a strong {
  margin-left: .5rem;
  margin-right: .5rem;
}

header nav img {
  margin: 1rem 0;
}

section header {
  width: 100%;
  padding-top: 0;
}

nav {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 7rem;
  font-weight: bold;
  display: flex;
}

nav ul {
  padding: 0;
  list-style: none;
}

nav ul li {
  text-align: left;
  margin: 0 .5rem;
  display: inline-block;
  position: relative;
}

input[readonly], textarea[readonly] {
  background-color: var(--color-bg-secondary);
}

* {
  scrollbar-width: thin;
  scrollbar-color: #cacae8 auto;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background-color: #cacae8;
  border-radius: 10px;
}

.group {
  justify-self: flex-start;
  margin: .5rem 1rem .5rem 0;
  display: inline-flex;
}

.group > * {
  margin: 0;
}

.group:not(.vertical) > * + * {
  margin-inline-start: -1px;
}

.group:not(.vertical) > :not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.group:not(.vertical) > :not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.group.vertical {
  flex-direction: column;
}

.group.vertical > * + * {
  margin-block-start: -1px;
}

.group.vertical > :not(:last-child) {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.group.vertical > :not(:first-child) {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

